<template>
  <div class="collect-page">
    <van-nav-bar title="我的收藏" fixed></van-nav-bar>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <ArticleItem
        v-for="article in list"
        :key="article.id"
        :article="article"
      />
    </van-list>
  </div>
</template>

<script>
import { getArticleCollect } from '@/api/article'

export default {
  name: 'collect-page',
  data() {
    return {
      list: [],
      finished: false,
      loading: false,
      page: 1,
    }
  },
  methods: {
    async onLoad() {
      const { data } = await getArticleCollect(this.page)
      this.list = this.list.concat(data.rows)
      this.loading = false
      this.page++

      //所有文章加载完毕，将finished设为true
      if (this.page > data.pageTotal) this.finished = true
    },
  },
}
</script>

<style lang="less" scoped>
.collect-page {
  margin-bottom: 50px;
  margin-top: 44px;
}
</style>
